import React from 'react';
import { Button, Card, Col } from "react-bootstrap";
import { useHistory } from 'react-router';

const Course = (props) => {

    const {id, title,description,img,date,teacherName} = props?.data;
    const history = useHistory();

    const handleCourse = (id) => {
      history.push(`/course/${id}/${title}`);
    };
    
    return (
      <Col md="4" className="mb-4">
        <Card>
          <Card.Img variant="top" src={img} />
          <Card.Body>
            <Card.Title>{title}</Card.Title>
            <Card.Text>{date}</Card.Text>
            <Card.Text>{description.slice(0, 130)}...</Card.Text>
            <Card.Text>{teacherName}...</Card.Text>
            <Button onClick={() => handleCourse(id, title)} variant="primary">
              View Course
            </Button>
          </Card.Body>
        </Card>
      </Col>
    );
};

export default Course;