import React, { createContext } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import "./App.css";
import { HashRouter as Router, Switch, Route } from "react-router-dom";
import Header from "./Components/Header/Header";
import Homepage from "./Components/HomePage/Homepage";
import Footer from "./Components/Footer/Footer";
import CoursesPage from "./Components/CoursesPage/CoursesPage";
import AboutUsPage from "./Components/AboutUsPage/AboutUsPage";
import ContactUsPage from "./Components/ContactUsPage/ContactUsPage";
import SingleCourse from "./Components/SingleCourse/SingleCourse";
import NotFound from "./Components/NotFound/NotFound";
export const Common = createContext("light");
const arr = [{ coursepage: 9 }, { homepage: 6 }];

function App() {
    return (
      <Common.Provider value={arr}>
        <Router>
          <Header></Header>
          <Switch>
            <Route exact path="/simple-website">
              <Homepage></Homepage>
            </Route>
            <Route exact path="/simple-website/home">
              <Homepage></Homepage>
            </Route>
            <Route exact path="/simple-website/courses">
              <CoursesPage></CoursesPage>
            </Route>
            <Route exact path="/simple-website/course/:courseId/:courseTitle">
              <SingleCourse></SingleCourse>
            </Route>
            <Route exact path="/simple-website/about">
              <AboutUsPage></AboutUsPage>
            </Route>
            <Route exact path="/simple-website/contact">
              <ContactUsPage></ContactUsPage>
            </Route>
            <Route path="*">
              <NotFound></NotFound>
            </Route>
          </Switch>
          <Footer></Footer>
        </Router>
      </Common.Provider>
    );
}

export default App;
