import React, { useContext } from 'react';
import { Common } from '../../App';
import Courses from '../Courses/Courses';

const CoursesPage = () => {

    const totalColumn = useContext(Common);
    const { coursepage } = totalColumn[0]; 

    return (
      <main>
        <Courses column={coursepage}></Courses>
      </main>
    );
};

export default CoursesPage;