import { Col, Container, Row } from "react-bootstrap";
import "./Countsection.css"
import CountUp from "react-countup";
import VisibilitySensor from "react-visibility-sensor";

const CountSection = () => {

  return (
    <section className="count-section bgoverlay">
      <Container fluid>
        <div className="py-5">
          <Row className="my-5">
            <Col md="12" className="text-center">
              <h2>Enhance your skills with best Online courses </h2>
              <h4>STARTING ONLINE LEARNING</h4>
            </Col>
          </Row>
          <Row className="my-5 text-center">
            <Col md="3">
              <div>
                <img src="" alt="" />
              </div>
              <div>
                <CountUp start={0} end={6500} duration={2}>
                  {({ countUpRef, start }) => (
                    <VisibilitySensor onChange={start}>
                      <h2 ref={countUpRef} />
                    </VisibilitySensor>
                  )}
                </CountUp>
                <h5>Students learning</h5>
              </div>
            </Col>
            <Col md="3">
              <div>
                <img src="" alt="" />
              </div>
              <div>
                <CountUp start={0} end={58263} duration={2}>
                  {({ countUpRef, start }) => (
                    <VisibilitySensor onChange={start}>
                      <h2 ref={countUpRef} />
                    </VisibilitySensor>
                  )}
                </CountUp>
                <h5>Graduates</h5>
              </div>
            </Col>
            <Col md="3">
              <div>
                <img src="" alt="" />
              </div>
              <div>
                <CountUp start={0} end={89663} duration={2}>
                  {({ countUpRef, start }) => (
                    <VisibilitySensor onChange={start}>
                      <h2 ref={countUpRef} />
                    </VisibilitySensor>
                  )}
                </CountUp>
                <h5>Free courses</h5>
              </div>
            </Col>
            <Col md="3">
              <div>
                <img src="" alt="" />
              </div>
              <div>
                <CountUp start={0} end={6500} duration={2}>
                  {({ countUpRef, start }) => (
                    <VisibilitySensor onChange={start}>
                      <h2 ref={countUpRef} />
                    </VisibilitySensor>
                  )}
                </CountUp>
                <h5>Active courses</h5>
              </div>
            </Col>
          </Row>
        </div>
      </Container>
    </section>
  );
};

export default CountSection;