import React from 'react';
import { Col, Container, Row } from 'react-bootstrap';

const ConatactTop = () => {
    return (
      <section>
        <Container fluid>
          <div className="pt-5">
            <Row className="text-center">
              <Col md="4">
                <span class="material-icons">face</span>
                <h2>Our Location</h2>
                <p>Collin Street West, Victor 8007, Australia.</p>
              </Col>
              <Col md="4">
                <span class="material-icons">face</span>
                <h2>Our Numbers</h2>
                <p>Mobile: (+096) 468 235</p>
              </Col>
              <Col md="4">
                <span class="material-icons">face</span>
                <h2>Our Email</h2>
                <p>info@edumy.com</p>
              </Col>
            </Row>
          </div>
        </Container>
      </section>
    );
};

export default ConatactTop;