import React from "react";
import { Col, Container, Row } from "react-bootstrap";
import "./Banner.css";
import logo from "./../Images/bannerLeftImage.png";

const Banner = () => {

  return (
    <section className="banner-section">
      <Container fluid>
        <div className="py-5">
          <Row>
            <Col md="5">
              <h2>Learn From Anywhere</h2>
              <p>
                Technology is bringing a massive wave of evolution on learning
                things on different ways.
              </p>
              <button type="button" className="btn">
                Get Started
              </button>
              <button type="button" className="btn">
                View Course
              </button>
            </Col>
            <Col md="7">
                  <img src={logo} alt="" />
            </Col>
          </Row>
        </div>
      </Container>
    </section>
  );
};

export default Banner;
