import React, { useEffect, useState } from "react";
import { Card, Container, Row, Button } from "react-bootstrap";
import {  useParams } from "react-router";

const SingleCourse = () => {
    const {courseId} = useParams()
    const [singleCourse, setSingleCourse] = useState([]);
    useEffect(() => {
      fetch("/courses.json")
        .then((res) => res.json())
        .then((data) => setSingleCourse(data));
    }, []);
    
    const singleData = singleCourse?.find(
      (courseData) => courseId == courseData
    .id);


  return (
    <section className="banner-section">
      <Container fluid>
        <div className="py-5">
          <Row>
            <Card>
              <Card.Img variant="top" src={singleData?.img} />
              <Card.Body>
                <Card.Title>{singleData?.title}</Card.Title>
                <Card.Text>{singleData?.description}</Card.Text>
                <Card.Text>
                  <h4>{singleData?.teacherName}</h4>
                </Card.Text>
                <Card.Text>
                  <h5>{singleData?.date}</h5>
                </Card.Text>
                <Button variant="primary">Go Back</Button>
              </Card.Body>
            </Card>
          </Row>
        </div>
      </Container>
    </section>
  );
};

export default SingleCourse;
